// this is one colored section of the page
function Section(props) {
  return (
    <div id={props.tag}>
      <div className="Section" style={{ '--image': `url(${props.image})` }}>
        {props.content || <p>{"SECTION " + props.number}</p>}
      </div>
    </div>
  );
}

// scrolling boxes 
class Testimonial extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      progress: 0,
    };

    this.arrowClick = this.arrowClick.bind(this);
  }

  // updates which slide you're on based on direction of arrow click (wraps around)
  arrowClick(direction) {
    var newNum = this.state.progress;

    if (direction) {
      if (newNum === this.props.quotes.length - 1) {
        newNum = 0;
      } else {
        newNum++;
      }
    } else {
      if (newNum === 0) {
        newNum = this.props.quotes.length - 1;
      } else {
        newNum--;
      }
    }

    this.setState({
      progress: newNum,
    });
  }

  render() {
    // these are the dots which indicate which slide you're currently on 
    var progressDots = [];
    for (let i = 0; i < this.props.quotes.length; i++) {
      progressDots.push(<span className="Quote-dot"
        style={{ backgroundColor: this.state.progress === i ? peach : darkBlue }}>
      </span>);
    }

    const num = this.state.progress; 
    var inside, head;
    if (this.props.type === 0) {
      head = <p className="Testimonial-head">How we do it...</p>;
      inside = <div className="Inner-box-div">
        <p className="Explanation">{this.props.quotes[num]}</p>
        <img src={this.state.progress % 2 === 1 ? example1 : example2} alt="example" className="Example-pic"></img>
      </div>;
    } else {
      const people = (this.props.type === 1) ? "Teachers" : "Students";
      head = <p className="Testimonial-head">
        {people} <FontAwesomeIcon icon={faHeart} style={{ color: red }} /> Us!
      </p>;
      inside = <div>
        <div className="Quote-div">
          <FontAwesomeIcon className="Quote-icon" icon={faQuoteLeft} />
          <p className="Quote">{this.props.quotes[num].quote}</p>
        </div>
        <div className="Quote-pic"></div>
        <div className="Quote-name-div">
          <p className="Quote-name">{this.props.quotes[num].person.toUpperCase()}</p>
          <p className="Quote-place">{this.props.quotes[num].place.toUpperCase()}</p>
        </div>
      </div>;
    }

    return (
      <div className={this.props.type === 1 ? "Testimonial-teacher" : "Testimonial"}>
        <div className="Quote-box">
          <p className="Arrow-icon left" onClick={() => this.arrowClick(0)}>
            <FontAwesomeIcon icon={faCaretLeft} />
          </p>
          <p className="Arrow-icon right" onClick={() => this.arrowClick(1)}>
            <FontAwesomeIcon icon={faCaretRight} />
          </p>
          {head}
          {inside}
          <div className="Dot-div">{progressDots}</div>
        </div>
      </div>
    );
  }
}

// expanding boxes for FAQ section
class DropDownBox extends React.Component {
  render() {
    return (
      <div className="Drop-down">
        <div className="Drop-down-box" onClick={() => this.props.handleClick()}>
          <p className="Drop-down-text">
            {this.props.question}
            <span className="Plus-minus"><FontAwesomeIcon icon={this.props.show ? faMinus : faPlus} /></span>
          </p>
        </div>
        {this.props.show &&
          <div className="Drop-down-box">
            <p className="Drop-down-text">{this.props.answer}</p>
          </div>}
      </div>
    );
  }
}

